import AppBar from '@material-ui/core/AppBar';
import Box from '@material-ui/core/Box';
import ButtonBase from '@material-ui/core/ButtonBase';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import NoSsr from '@material-ui/core/NoSsr';
import Slide from '@material-ui/core/Slide';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import { makeStyles } from '@material-ui/styles';
import classNames from 'classnames';
import Head from 'next/head';
import Link from 'next/link';
import { withRouter } from 'next/router';
import PropTypes from 'prop-types';
import React, { useState } from 'react';

import CodeIcon from 'components/CodeIcon';
import HomeIcon from 'components/HomeIcon';
import LinkWrapper from 'components/LinkWrapper';
import MenuIcon from 'components/MenuIcon';
import MusicIcon from 'components/MusicIcon';
import VideosIcon from 'components/VideosIcon';
import WritingIcon from 'components/WritingIcon';

import styles from './SitePage.css';

const IMAGE = `/static/images/headshot.jpg`;
const IMAGE_ALT = `Cedric McDougalâ€™s headshot`;

const FISH_HELMET_240 = `https://cedricmcdougal.s3.us-east-2.amazonaws.com/personal-site/images/fish-helmet-240.png`;
const FISH_HELMET_480 = `https://cedricmcdougal.s3.us-east-2.amazonaws.com/personal-site/images/fish-helmet-480.png`;

const PAGES = [
  { label: `Code`, href: `/code`, Icon: CodeIcon },
  { label: `Music`, href: `/music`, Icon: MusicIcon },
  { label: `Videos`, href: `/videos`, Icon: VideosIcon },
  { label: `Writing`, href: `/writing`, Icon: WritingIcon },
];

const DRAWER = [{ label: `Hello!`, href: `/`, Icon: HomeIcon }, ...PAGES];

const SitePage = ({
  children,
  description,
  favicon,
  icon,
  mobileTitle,
  router,
  title,
}) => {
  const PageIcon = icon;
  const classes = makeStyles(styles)();
  const [drawerIsOpen, setDrawerIsOpen] = useState(false);
  const trigger = useScrollTrigger();

  const selectedPage = PAGES.find(({ href }) => {
    return router.pathname === href;
  });

  return (
    <>
      <CssBaseline />
      <Head>
        <title key="title">{title}</title>
        <meta key="description" content={description} name="description" />
        <meta key="og:type" content="website" property="og:type" />
        <meta key="og:title" content={title} property="og:title" />
        <meta
          key="og:description"
          content={description}
          property="og:description"
        />
        <meta key="og:image" content={IMAGE} property="og:image" />
        <meta key="og:image:alt" content={IMAGE_ALT} property="og:image:alt" />
        {/* Files from realfavicongenerator.net */}
        <link
          href={`/static/favicons/${favicon}/apple-touch-icon.png`}
          rel="apple-touch-icon"
          sizes="180x180"
        />
        <link
          href={`/static/favicons/${favicon}/favicon-32x32.png`}
          rel="icon"
          sizes="32x32"
          type="image/png"
        />
        <link
          href={`/static/favicons/${favicon}/favicon-16x16.png`}
          rel="icon"
          sizes="16x16"
          type="image/png"
        />
        <link
          href={`/static/favicons/${favicon}/site.webmanifest`}
          rel="manifest"
        />
        <link
          color="#5bbad5"
          href={`/static/favicons/${favicon}/safari-pinned-tab.svg`}
          rel="mask-icon"
        />
        <link
          href={`/static/favicons/${favicon}/favicon.ico`}
          rel="shortcut icon"
        />
        <meta content="#da532c" name="msapplication-TileColor" />
        <meta
          href={`/static/favicons/${favicon}/browserconfig.xml`}
          name="msapplication-config"
        />
      </Head>
      <Slide appear={false} direction="down" in={!trigger}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <div className={classes.left}>
              <Link href="/" passHref prefetch>
                <ButtonBase className={classes.title} component="a" focusRipple>
                  <PageIcon className={classes.titleIcon} />
                  <Typography className={classes.titleText} variant="h6">
                    <span className={classes.desktopTitle}>
                      Cedric McDougal
                    </span>
                    <span className={classes.mobileTitle}>{mobileTitle}</span>
                  </Typography>
                </ButtonBase>
              </Link>
              <NoSsr>
                <img
                  alt="Cedric in a hat that looks like an anglerfish"
                  className={classes.fishHelmet}
                  src={FISH_HELMET_480}
                  srcSet={`${FISH_HELMET_240} 240w, ${FISH_HELMET_480} 480w`}
                />
              </NoSsr>
            </div>
            <Tabs
              className={classes.tabs}
              TabIndicatorProps={{
                style: { display: `none` },
              }}
              value={selectedPage ? selectedPage.href : false}>
              {PAGES.map(({ label, href }) => {
                return (
                  <Tab
                    key={label}
                    classes={{
                      selected: classes.selected,
                    }}
                    className={classes.tab}
                    component={LinkWrapper}
                    label={
                      <>
                        {label}
                        <div className={classes.underline} />
                      </>
                    }
                    LinkProps={{
                      href,
                      passHref: true,
                      prefetch: true,
                    }}
                    value={href}
                  />
                );
              })}
            </Tabs>
            <IconButton
              className={classes.menuButton}
              onClick={() => {
                setDrawerIsOpen(true);
              }}>
              <MenuIcon className={classes.menuIcon} />
            </IconButton>
            <Drawer
              anchor="right"
              onClose={() => {
                setDrawerIsOpen(false);
              }}
              open={drawerIsOpen}>
              <List className={classes.drawerList}>
                {DRAWER.map(({ label, href, Icon }) => {
                  return (
                    <Link key={label} href={href} passHref prefetch>
                      <ListItem
                        button
                        classes={{
                          selected: classes.selected,
                        }}
                        className={classes.drawerListItem}
                        component="a"
                        selected={href === selectedPage.href}>
                        <ListItemIcon className={classes.drawerListItemIcon}>
                          <Icon className={classes.drawerListIcon} />
                        </ListItemIcon>
                        <ListItemText primary={label} />
                      </ListItem>
                    </Link>
                  );
                })}
              </List>
            </Drawer>
          </Toolbar>
        </AppBar>
      </Slide>
      <Container maxWidth="xl">
        <Box mb={8} mt={{ xs: 7, sm: 14 }}>
          {children}
          <List className={classes.mobileFooter}>
            {PAGES.map(({ label, href }) => {
              return (
                <ListItem
                  key={label}
                  button
                  component={LinkWrapper}
                  LinkProps={{
                    href,
                    passHref: true,
                    prefetch: true,
                  }}
                  value={href}>
                  <ListItemText
                    className={classes.mobileFooterText}
                    primary={label}
                    primaryTypographyProps={{
                      color: `primary`,
                    }}
                  />
                </ListItem>
              );
            })}
          </List>
        </Box>
      </Container>
    </>
  );
};

SitePage.propTypes = {
  description: PropTypes.string.isRequired,
  favicon: PropTypes.oneOf([`home`, `code`, `music`, `videos`, `writing`])
    .isRequired,
  icon: PropTypes.func.isRequired,
  mobileTitle: PropTypes.string.isRequired,
  router: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
};

export default withRouter(SitePage);
